"use client";
import { useState } from 'react';
import { useFinance } from '@/context/FinanceContext';
import TransactionForm from '@/components/forms/TransactionForm';
import AccountsGrid from '@/components/dashboard/AccountsGrid';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Dialog, DialogContent, DialogTrigger } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { PlusCircle, TrendingDown, TrendingUp, PiggyBank } from 'lucide-react';
import { startOfMonth, isSameMonth, isSameYear, isSameDay } from 'date-fns';
// Nota: Importa tus componentes de gráficas aquí si los creas (FinanceChart)

export default function Dashboard() {
  const { transactions } = useFinance();
  const [period, setPeriod] = useState('month');

  // Filtrado de lógica básica
  const now = new Date();
  const filteredTx = transactions.filter(t => {
    const tDate = new Date(t.date);
    if (period === 'month') return isSameMonth(tDate, now);
    if (period === 'year') return isSameYear(tDate, now);
    if (period === 'day') return isSameDay(tDate, now);
    return true;
  });

  const income = filteredTx.filter(t => t.type === 'income').reduce((acc, curr) => acc + curr.amount, 0);
  const expense = filteredTx.filter(t => t.type === 'expense').reduce((acc, curr) => acc + curr.amount, 0);
  const savings = filteredTx.filter(t => t.categoryId === 'exp1' && t.subCategoryId === 'Ahorro').reduce((acc, curr) => acc + curr.amount, 0); // Lógica de ejemplo
  const profit = income - expense;
  
  // Cálculo de Porcentajes
  const expensePct = income > 0 ? (expense / income) * 100 : 0;
  const savingPct = income > 0 ? (savings / income) * 100 : 0;

  return (
    <div className="container mx-auto p-4 max-w-5xl space-y-6 pb-24">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-indigo-400 to-cyan-400">
          Hola, CEO Noxum
        </h1>
        <Dialog>
          <DialogTrigger asChild>
            <Button className="bg-indigo-600 hover:bg-indigo-700">
              <PlusCircle className="mr-2 h-4 w-4" /> Agregar
            </Button>
          </DialogTrigger>
          <DialogContent className="bg-zinc-950 border-zinc-800">
            <TransactionForm />
          </DialogContent>
        </Dialog>
      </div>

      <AccountsGrid />

      <Tabs defaultValue="month" onValueChange={setPeriod} className="space-y-4">
        <TabsList className="bg-zinc-900 border border-zinc-800">
          <TabsTrigger value="day">Día</TabsTrigger>
          <TabsTrigger value="month">Mes Actual</TabsTrigger>
          <TabsTrigger value="year">Año 2025</TabsTrigger>
        </TabsList>

        <TabsContent value={period} className="space-y-4">
          {/* Tarjetas de Resumen KPI */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <KPICard title="Ingresos" value={income} icon={<TrendingUp className="text-green-500" />} color="text-green-500" />
            <KPICard title="Gastos" value={expense} icon={<TrendingDown className="text-red-500" />} color="text-red-500" subtext={`${expensePct.toFixed(1)}%`} />
            <KPICard title="Ahorro" value={savings} icon={<PiggyBank className="text-blue-500" />} color="text-blue-500" subtext={`${savingPct.toFixed(1)}%`} />
            <KPICard title="Restante" value={profit} icon={<Wallet className="text-purple-500" />} color="text-white" />
          </div>

          {/* Tabla Avanzada Simplificada */}
          <Card className="bg-zinc-900 border-zinc-800">
            <CardHeader><CardTitle>Detalle de Movimientos</CardTitle></CardHeader>
            <CardContent>
              <div className="space-y-4">
                {filteredTx.length === 0 ? (
                  <p className="text-zinc-500 text-center py-4">No hay datos para este periodo.</p>
                ) : (
                  filteredTx.map(tx => (
                    <div key={tx.id} className="flex justify-between items-center border-b border-zinc-800 pb-2 last:border-0">
                      <div>
                        <p className="font-medium text-white">{tx.description || 'Sin detalle'}</p>
                        <p className="text-xs text-zinc-500">{new Date(tx.date).toLocaleDateString()}</p>
                      </div>
                      <span className={tx.type === 'income' ? 'text-green-400 font-mono' : 'text-red-400 font-mono'}>
                        {tx.type === 'income' ? '+' : '-'}${tx.amount.toLocaleString()}
                      </span>
                    </div>
                  ))
                )}
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}

function KPICard({ title, value, icon, color, subtext }: any) {
  return (
    <Card className="bg-zinc-900 border-zinc-800">
      <CardContent className="p-4 flex flex-col justify-between h-full">
        <div className="flex justify-between items-start">
          <span className="text-zinc-400 text-sm">{title}</span>
          {icon}
        </div>
        <div>
          <span className={`text-xl font-bold ${color}`}>${value.toLocaleString()}</span>
          {subtext && <p className="text-xs text-zinc-500 mt-1">{subtext}</p>}
        </div>
      </CardContent>
    </Card>
  );
}